@font-face {
  font-family: 'TheJamsil5Bold';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302_01@1.0/TheJamsil5Bold.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}



* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'TheJamsil5Bold';
}

.state {
  max-width: 640px;
  width: calc(100% - 20px);
  margin: 100px auto;
  border: 2px solid #d9d9d9;
  padding: 30px;
  text-align: center;
}

.state h1 {
  color: #333;
  margin-bottom: 20px;
}

.state input {
  height: 30px;
  border-radius: 50px;
  border: 1px solid #ddd;
  margin-right: 5px;
  padding: 10px;
}

.state button {
  height: 40px;
  width: 40px;
  border-radius: 50px;
  background-color: #000;
  border: none;
  color: #fff;
}

.state p {
  margin-top: 20px;
}

.state p span {
  font-size: 13px;
  color: darkblue;
}